.sms-form {
    text-align: left;
    padding: 1em;
}
.sms-form label {
    display: block;
}
.sms-form input,
.sms-form textarea {
    font-size: 1em;
    width: 100%;
    box-sizing: border-box;
}
.sms-form div {
    margin-bottom: 0.5em;
}
.sms-form button {
    font-size: 1em;
    width: 200px;
    margin:0 auto;
    display:block;
}
.sms-form.error {
    outline: 2px solid #f00;
}